<template>
  <b-row class="mb-1">
    <b-col style="text-align: left">
      <b-form>
        <b-form-group id="userid-group" label="ID" label-for="userid">
          <b-form-input
            id="userid"
            v-model="user.userid"
            type="text"
            required
            disabled
            placeholder="아이디 입력"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="userpwd-group" label="비밀번호" label-for="userpwd">
          <b-form-input
            id="userpwd"
            v-model="user.userpwd"
            type="password"
            required
            placeholder="비밀번호 입력"
          ></b-form-input>
        </b-form-group>

        <b-form-group id="username-group" label="이름" label-for="username">
          <b-form-input
            id="username"
            v-model="user.username"
            type="text"
            required
            placeholder="이름 입력"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="email-group" label="이메일" label-for="email">
          <b-form-input
            id="email"
            v-model="user.email"
            type="text"
            required
            placeholder="이메일 입력"
          ></b-form-input>
        </b-form-group>

        <b-button variant="primary" class="m-1" @click="onSubmit"
          >수정</b-button
        >
        <b-button variant="danger" class="m-1" @click="moveMyPage"
          >뒤로가기</b-button
        >
      </b-form>
    </b-col>
  </b-row>
</template>

<script>
import { getById, modify } from "@/api/member";
import { mapState, mapActions } from "vuex";

const memberStore = "memberStore";

export default {
  name: "MemberModify",
  data() {
    return {
      user: {
        userid: "",
        username: "",
        userpwd: "",
        email: "",
        joindate: "",
      },
    };
  },
  computed: {
    ...mapState(memberStore, ["userInfo"]),
  },
  created() {
    getById(
      this.userInfo.userid,
      (response) => {
        this.user.userid = this.userInfo.userid;
        this.user.userpwd = response.data.userpwd;
        this.user.username = this.userInfo.username;
        this.user.email = this.userInfo.email;
      },
      (error) => {
        console.log("회원정보 가져오던 중 에러 발생", error);
      },
    );
  },
  methods: {
    ...mapActions(memberStore, ["setUserInfo"]),
    onSubmit(event) {
      event.preventDefault();
      let err = true;
      let msg = "";
      !this.user.userpwd && ((msg = "비밀번호를 입력해주세요"), (err = false)),
        err &&
          !this.user.username &&
          ((msg = "이름을 입력해주세요"), (err = false)),
        err &&
          !this.user.email &&
          ((msg = "이메일을 입력해주세요"), (err = false));
      if (!err) alert(msg);
      else this.modifyMember();
    },
    modifyMember() {
      modify(
        {
          userid: this.user.userid,
          username: this.user.username,
          userpwd: this.user.userpwd,
          email: this.user.email,
        },
        ({ data }) => {
          let msg = "수정 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "수정이 완료되었습니다.";
            this.setUserInfo({
              username: this.user.username,
              email: this.user.email,
              joindate: this.userInfo.joindate,
              userid: this.user.userid,
            });
          }
          alert(msg);
          // 현재 route를 /list로 변경.
          this.moveMyPage();
        },
        (error) => {
          console.log(error);
        },
      );
    },
    moveMyPage() {
      this.$router.push({
        name: "mypage",
        params: {
          userid: this.userInfo.userid,
        },
      });
    },
  },
};
</script>

